.postWrapper {
    /* margin-left: 300px; */
    min-width: 400px;
    width: 70%;
    height: auto;
    background-color: #FFFFFF;
    border-radius: 10px;
    box-shadow: 0 1px 16px 0 rgba(0, 0, 0, 0.2);
    padding-bottom: 10px;
}

.postTop {
    padding-left: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    /* background-color: yellow; */
}

/* .postTopContainer {
    width: 59%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
} */

.postTopContainerWrapper {
    display: flex;
}

.postTopContainerLeft {
    flex: 9;
    /* background-color: blue; */
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.postTopContainerRight {
    flex: 3;
    /* background-color: pink; */
    text-align: center;
    align-items: center;
}

.postProfileImg {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
}

.postProfileText {
    padding-left: 10px;
    padding-right: 10px;
    font-weight: bold;
}

.postSwapButtonWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

.postSwapButton {
    display: flex;
    /* flex-direction: column; */
    background-color: #FFAB03;
    width: 80%;
    height: 40px;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin-left: -40px;
    min-width: 120px;
    
}

.postSwapButtonText {
    color: white;
}

.deleteButtonWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

.deleteButton {
    display: flex;
    /* flex-direction: column; */
    background-color: #FF3F03;
    width: 80%;
    height: 40px;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin-left: -40px;
    min-width: 120px;
    
}

.deleteButtonText {
    color: white;
}

.chatIcon {
    color: white;
    margin-right: 5px;
}

.postMiddle {
    padding-left: 20px;
    padding-right: 20px;
}

.postMiddleDescription {
    padding-bottom: 10px;
}

.postMiddleDescriptionContainer {
    /* background-color: green; */
}

.postMiddleItemLikesCommentsContainer {
    display: flex;
    /* background-color: cadetblue; */
    padding-top: 10px;
    padding-bottom: 10px;
}

.postMiddleItemLikes {
    /* background-color: purple; */
    padding-right: 10px;
}

.numberLikes {
    /* background-color: green; */
    padding-left: 5px;
}

.numberComments {
    padding-left: 5px;
}

.postCommentInputWrapper {
    /* background-color: coral; */
    padding-left: 20px;
}
.postCommentInput {
    /* width: 600px; */
    width: 97%;
    height: 40px;
    /* background-color: white; */
    /* background-color: green; */
    border-radius: 10px;
    display: flex;
    align-items: center;
    border: none;
    /* margin-left: 70px; */
    /* margin-top: 16px;
    margin-left: 0px;
    margin-right: 100px; */
}

.postCommentContentSection {
    padding-left: 10px;
    padding-right: 10px;
    height: 200px;
    overflow-y: auto;
}