.swapAwayPrompt {
  background-color: #f5f5f7;
  width: 390px;
  height: 150px;
  margin-left: 100px;
  position: absolute;
}

.swapAwayUserImg {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-left: -10px;
  position: absolute;
  cursor: pointer;
}

.swapAwayUsername {
  margin-top: 15px;
  position: static;
}

.swapAwayButton {
  display: flex;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  margin-left: 70px;
  height: 50px;
  width: 200px;
  position: absolute;
}

.swapAwayButtonTextGroup {
  margin-left: -10px;
  color: white;
  font-weight: bold;
}
.swapAwayButtonText {
  margin-left: 10px;
}
